import React, { useState } from 'react'
import { ModalForm, ProFormText } from '@ant-design/pro-form';

import { useAllData } from '../../state/guard/hooks'
import { useDidContract } from '../../hooks/useContract'
import { ExclamationCircleOutlined } from '@ant-design/icons';
import { useActiveWeb3React } from '../../hooks'

import { isAddress } from 'ethers/lib/utils'

import { Table, Space, Modal, Pagination, Button } from 'antd'
import { Layout } from 'antd';
import styled from 'styled-components'

const { Content } = Layout;

const Pager = styled.div`
  display: flex;
  flex-direction: column;
  margin-top: 26px; 
`

const Body = styled.div`
  display: flex;
  flex-direction: column;
  margin: 26px; 
  padding: 30px 25px; 
  background: white;
`

export default function NftTable() {
  // reset states on back

  const { library } = useActiveWeb3React()

  const didContract = useDidContract()

  async function onAgree(address: string) {
    if (!library || !didContract) throw new Error('missing dependencies')
    if (!isAddress(address)) throw new Error('address error')
    let result = await didContract.accept([address])
    console.log('result', result)
  }

  async function onRemove(address: string) {
    if (!library || !didContract) throw new Error('missing dependencies')
    if (!isAddress(address)) throw new Error('address error')
    console.log('address',address)
    let result = await didContract.revoke([address])
    console.log('result', result)
  }

  const dataList = useAllData()

  const [list, setList] = useState([])
  const [showModal1Visible, setModal1Visible] = useState(false)
  const [showUserInfo, setUserInfo] = useState(undefined)
  const [visible, setVisible] = useState(false)

  const content1 = '移除白名单会使用户NFT身份失效，并无法再通过DEX进行任何交易，请谨慎操作'
  const content2 = '会向用户发放户NFT通行证，并激活DEX交易权限，请谨慎操作'

  function getTime(timestamp: any) {
    if (timestamp == '0')
      return '-'
    var day = "",
      month = "",
      hours = "",
      minutes = "",
      seconds = "",
      d = new Date(Number(timestamp) * 1000)
    day = d.getDate() < 10 ? '0' + d.getDate() : d.getDate().toString()
    month = d.getMonth() + 1 < 10 ? '0' + (d.getMonth() + 1) : (d.getMonth() + 1).toString()
    hours = d.getHours() < 10 ? '0' + d.getHours() : d.getHours().toString()
    minutes = d.getMinutes() < 10 ? '0' + d.getMinutes() : d.getMinutes().toString()
    seconds = d.getSeconds() < 10 ? '0' + d.getSeconds() : d.getSeconds().toString()
    var date = (d.getFullYear() + "-" + month + "-" + day + " " + hours + ":" + minutes + ":" + seconds);
    return date
  }

  function getState(state: any) {
    if (state === 'APPLYING') {
      return '申请中'
    } else if (state === 'ISSUE') {
      return '已发放'
    } else if (state === 'REVOKE') {
      return '已撤销'
    }
    return '-'
  }

  const columns = [
    {
      title: '地址',
      dataIndex: 'owner',
      key: 'owner',
    },
    {
      title: '邮箱',
      dataIndex: 'email',
      key: 'email',
    },
    {
      title: 'NFT发放时间',
      key: 'approveTime',
      render: (t: any) => (
        getTime(t.approveTime)
      ),
    },
    {
      title: 'NFT移除时间',
      key: 'revokeTime',
      render: (t: any) => (
        getTime(t.revokeTime)
      ),
    },
    {
      title: '状态',
      key: 'state',
      render: (t: any) => (
        getState(t.state)
      ),
    },
    {
      title: '操作',
      key: 'action',
      render: (t: any) => (
        <Space size="middle">
          <a
            onClick={() => {
              setUserInfo(t)
              setModal1Visible(true)
            }}
          >查看</a>
          {t.state === 'APPLYING' || t.state === 'ISSUE' ?
            <a
              onClick={() => {
                Modal.confirm({
                  title: t.state === 'APPLYING' ? '发放NFT' : '移除NFT',
                  icon: <ExclamationCircleOutlined />,
                  content: t.state === 'APPLYING' ? content2 : content1,
                  okText: '确认',
                  cancelText: '取消',
                  onOk: () => {
                    if (t.state === 'APPLYING') {
                      onAgree(t.owner)
                    } else {
                      onRemove(t.owner)
                    }
                  }
                })
              }}
            >{t.state === 'APPLYING' ? '同意' : '移除'}</a> : <a />}
        </Space>
      ),
    },
  ];

  const pageSize = 15
  var pageIndex = 1

  function getList() {
    if (dataList && dataList.length === 0) {
      return []
    }
    if (pageIndex * pageSize <= dataList.length) {
      return dataList.slice((pageIndex - 1) * pageSize, pageIndex * pageSize)
    } else {
      return dataList.slice((pageIndex - 1) * pageSize, dataList.length)
    }
  }

  function onChange(pageNumber: any) {
    pageIndex = pageNumber
    setList(getList())
  }

  return (
    <Body>
      <Button
        type="primary"
        onClick={() => setVisible(true)}
        style={{ width: '100px', margin: '0 0 25px auto' }}>
        发放NFT
      </Button>
      <Content>
        <Table
          columns={columns}
          dataSource={dataList ? list.length > 0 ? list : getList() : []}
          pagination={false}
        />
        {dataList && dataList.length ?
          <Pager>
            <Pagination
              total={dataList?.length}
              pageSize={pageSize}
              defaultCurrent={1}
              style={{ marginLeft: 'auto' }}
              onChange={onChange}
            />
          </Pager>
          : ""
        }
        <Modal
          title="用户信息"
          visible={showModal1Visible}
          onOk={() => setModal1Visible(false)}
          onCancel={() => setModal1Visible(false)}
        >
          <p><h4>地址: </h4>{showUserInfo?.['owner']}</p>
          <p><h4>邮箱: </h4>{showUserInfo?.['email']}</p>
          <p><h4>NFT发放时间: </h4>{getTime(showUserInfo?.['approveTime'])}</p>
          <p><h4>NFT移除时间: </h4>{getTime(showUserInfo?.['revokeTime'])}</p>
          <p><h4>用户状态: </h4>{getState(showUserInfo?.['state'])}</p>
        </Modal>
        <ModalForm
          title="发放NFT"
          visible={visible}
          onVisibleChange={setVisible}
          width={'500px'}
          onFinish={async (value) => {
            let address = value.name
            if (!isAddress(address)) throw new Error('address error')
            if (!didContract) throw new Error('missing dependencies')
            let result = await didContract.issue([address])
            console.log('result', result)
          }}
        >
          <ProFormText
            rules={[
              {
                required: true,
                message: '地址为必填项',
              },
            ]}
            width="md"
            placeholder={'请输入地址'}
            name="name"
          />
        </ModalForm>
      </Content>
    </Body>
  )
}